<template>
	<view class="filter-container">
		<ul class="area-list">
			<li v-for="(area,index) in areaList" 
				:key="area.id"
				@click="goArea(index)"
				:class="areaIndex==index ? 'li-active' : ''"
				>
					{{ area.name }}
			</li>
		</ul>
		
		<view class="type-filter">
			<view class="filter-item">
				<view class="text">
					<text>疑惑</text>
				</view>
				<view class="icon">
					<text class="iconfont icon">&#xe604;</text>
				</view>
			</view>
		</view>
		<view>
			<button @click="open">打开弹窗</button>
			<uni-popup ref="popup" type="bottom">底部弹出 Popup</uni-popup>
		</view>
		
		</view>
	</view>
</template>

<script>
	export default {
		name:"home-filter",
		data() {
			return {
				areaList:[
					{id:1,name:'全国'},
					{id:2,name:'深圳'},
					{id:3,name:'广州'},
					{id:4,name:'哈尔滨'},
					{id:5,name:'内蒙古'},
					{id:6,name:'武汉'},
					{id:7,name:'成都'},
					{id:8,name:'重庆'},
					{id:9,name:'大理'}
				],
				areaIndex:0
			}
		},
		methods:{
			goArea(index){
				this.areaIndex = index
			},
			open(){
				// 通过组件定义的ref调用uni-popup方法 ,如果传入参数 ，type 属性将失效 ，仅支持 ['top','left','bottom','right','center']
				this.$refs.popup.open('bottom')
			}
		}
	}
</script>

<style lang="less" scoped>
.filter-container{
	.area-list{
		padding: 0;
		margin: 0;
		list-style: none;
		height: 100rpx;
		width: 95%;
		margin: 0 auto;
		margin-top: 40rpx;
		display: flex;
		overflow: auto;
		align-items: center;
		text-align: center;
		&::-webkit-scrollbar {
			display: none;
		}
		li{
			flex-shrink: 0;
			width: 120rpx;
			color: #888;
			font-size: 14px;
		}
		.li-active{
			font-size: 20px;
			color: #333;
		}
	}
	.type-filter{
		.filter-item{
			display: flex;
			width: 100rpx;
			// background-color: #333;
			justify-content: space-evenly;
			align-items: center;
			.text{
				font-size: 14px;
				color: #333;
			}
			.icon{
				color: #bbb;
				font-size: 6px;
			}
		}
	}
}
</style>